import React from 'react';
import axios from 'axios';
import styles from  '../css/login.css';
import { Nav, Navbar, Form, FormControl,NavDropdown } from 'react-bootstrap';
import { Dropdown,Icon,Button,Card,Divider,Segment,Checkbox, Input, Label,Loading,Loader, Dimmer,List,Table, Container, Modal } from 'semantic-ui-react';



class Board extends React.Component {
    
    constructor() {
        super();
        this.state = {
            user:this.props,
            classes: null,
            colors:['teal','blue','purple','cyan'],
            assignments:[
                {
                    id:1,
                    classID:3136,
                    name:'Lab 1',
                    dueDate: '2021-05-10',
                    assigned:'2021-05-01',
                    status:1,
                    link:'https://maxwelljones.ca'
                },
                {
                    id:2,
                    classID:3106,
                    name:'Project 1',
                    dueDate: '2021-05-17',
                    assigned:'2021-05-01',
                    status:0,
                    link:'https://maxwelljones.ca'
                },
                {
                    id:3,
                    classID:3809,
                    name:'Swing Quiz',
                    dueDate: '2021-05-19',
                    assigned:'2021-05-01',
                    status:1,
                    link:'https://maxwelljones.ca'
                }
            ]
        }
        
    }

    async getData(user_id){
       
        axios.get('https://maxwelljonesdesign.com/classes/'+user_id )
        .then(res => {
          if(res.data){
            this.setState({classes: res.data});
            
          }
          else{
           
          }
      })
    }
    componentDidMount() {
        this.getData(this.props.user.id);
    }

    logout(){
        console.log("logging out");
        localStorage.setItem("user",null);
        this.setState({user: null});
        window.location.reload(true);
    }

    createClass(){
        var name = prompt("Please enter the name of the class");
        var prof = prompt("Please enter the prof of the class");

        let classObject = {
            user_id:this.props.user.id,
            name:name,
            prof:prof
        }
        axios.post('https://maxwelljonesdesign.com/classes/create',classObject )
        .then(res => {
          if(res.data){
              console.log("success");
              window.location.reload(true);
          }
          else{
            console.log("fail");
          }
      })
    }

    deleteClass(e){
        console.log(e);
        axios.get('https://maxwelljonesdesign.com/classes/delete/'+e.id)
        .then(res => {
            if (res.status === 200) {
                console.log("success");
              window.location.reload(true);
            }
          else{
            console.log("fail");
          }
      })
    }
    navBar(){
        return(
            <div style={{width:'100%',padding:'.75rem',backgroundColor:'rgba(33,43,78,255)',height:'7vh'}}>
                    
                    <Button style={{backgroundColor:'rgba(33,53,108,255)',color:'white'}}>
                        <Dropdown text='Classes' pointing>
                            <Dropdown.Menu>
                                <Dropdown.Item onClick={()=>this.createClass()}>Create</Dropdown.Item>
                                <Dropdown.Item>
                                    <Dropdown text='Delete' pointing >
                                    <Dropdown.Menu>
                                        {this.state.classes.map(item=>
                                            <Dropdown.Item key={item.id} onClick={()=>this.deleteClass(item)}>{item.name}</Dropdown.Item>
                                        )}
                                    </Dropdown.Menu>
                        </Dropdown></Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    </Button>
                    <Button onClick={()=>this.logout()} style={{float:'right',margin:'auto',textAlign:'center',backgroundColor:'rgba(33,53,108,255)',color:'white',marginRight:'5px'}}><Icon style={{textAlign:'center',margin:'auto'}} name='unlock'/></Button>
                    
                </div>
        );
    } 
    getDate(date){
        date = new Date(date);
        let weekday = ['Sun','Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
        let dayOfWeek = weekday[date.getUTCDay()];
        let formatedDate = (date.getUTCMonth()+1)+'/'+(date.getUTCDate() < 9 ? ('0'+date.getUTCDate()): date.getUTCDate()) + ' '+dayOfWeek;
        return formatedDate;
    }
    getToday(){

        var objToday = new Date(),
        weekday = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
        dayOfWeek = weekday[objToday.getDay()],
        dayOfMonth =  ( objToday.getDate() < 10) ? '0' + objToday.getDate() : objToday.getDate(),
        months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
        curMonth = months[objToday.getMonth()],
        curYear = objToday.getFullYear(),
        curHour = objToday.getHours() > 12 ? objToday.getHours() - 12 : (objToday.getHours() < 10 ? "0" + objToday.getHours() : objToday.getHours()),
        curMinute = objToday.getMinutes() < 10 ? "0" + objToday.getMinutes() : objToday.getMinutes(),
        curMeridiem = objToday.getHours() > 12 ? "pm" : "am";
        var today = curHour + ":" + curMinute + curMeridiem + " " + dayOfWeek + " " + dayOfMonth + " " + curMonth + ", " + curYear;
        return today;
    }
    tableBoard(classes){
      
        return (
            classes.map(item => 
                    <Table.Row  color='blue' key={item.id}>
                        <Table.Cell id='tbody'>{item.classID}</Table.Cell>
                        <Table.Cell id='tbody'>{item.name}</Table.Cell>
                        <Table.Cell id='tbody'>{item.status !== 0 ? 'Bad' : 'Good' }</Table.Cell>
                        <Table.Cell id='tbody'>{this.getDate(item.dueDate)}</Table.Cell>
                    </Table.Row>
                )
        )
    }

    classesComponent(){
        let colors = this.state.colors;
        let counter = -1;
        return(
            <div>
                <div style={{width:'100%',overflowY:'auto',padding:'3vh',maxWidth:'600px'}}>

                    <Card id='cardMain'>
                        <h1 style={{fontSize:'15px',textAlign:'center',fontFamily:'monospace'}}>{this.getToday()}</h1>
                        <Button.Group style={{marginBottom:'0.5vh'}}>
                            <Button color='orange'>New Assignment</Button>
                        </Button.Group>
                        <Divider style={{margin:'5px'}}></Divider>
                        <div style={{maxHeight:'500px',overflow:'auto'}}>
                            <Table unstackable>
                                <Table.Header  color='blue'>
                                <Table.Row>
                                    <Table.HeaderCell id='thc'>Class</Table.HeaderCell>
                                    <Table.HeaderCell id='thc'>Name</Table.HeaderCell>
                                    <Table.HeaderCell id='thc'>Status</Table.HeaderCell>
                                    <Table.HeaderCell id='thc'>Due Date</Table.HeaderCell>
                                </Table.Row>
                                </Table.Header>
                                <Table.Body id='tbody'>
                                    {this.tableBoard(this.state.assignments)}
                                </Table.Body>
                            </Table>
                        </div>
                        <Divider style={{margin:'1vh'}}></Divider>
                        <h3 style={{margin:'0px'}}>Classes</h3>
                        <div style={{padding:'1vh',textAlign:'left'}}>
                        {/* backgroundColor:colors[counter=counter+1] */}
                            {this.state.classes.map(item =>
                                <Label style={{margin:'3px',color:'white',width:'100%'}} color='blue' key={item.id}>
                                    <div style={{float:'left',fontSize:'110%'}}>{item.name}</div>
                                    <div style={{float:'right',color:'rgb(33, 43, 78)',fontWeight:'bold!important',fontSize:'110%'}}>{item.prof}</div>
                                </Label>
                            )}
                            
                        </div>

                    </Card>

                    <div style={{margin:'5px',textAlign:'center'}}>
                        <p style={{textAlign:'center',color:'white',marginTop:'-5px'}}><a style={{color:'white',opacity:'50%'}} href = "mailto:maxjones2001@hotmail.com?subject=Help">Need help?</a></p>
                    </div>
                    
                </div>
               
            </div>
        );
    }

    render() {
        
        return(
            <div id='noscroll'>
                {this.state.classes && this.navBar()}
                {this.state.classes && this.classesComponent()}
               
            </div>
        );
    }      
}

export default Board;